<template lang="pug">

  #result(:class="{true : isShow}")
    p
      button.pure-button(type="button" @click="goBack") back

    ul(v-if="resData.length !== 0")
      li(v-for="item in resData")
        h2(v-text="item.name")
        p(v-text="'[ 常见释义 ] : ' + item.transContent")

    p(v-else).
      暂无 <strong v-text="keyWord"></strong> 的查询结果，<a>点击添加</a>此词条信息

</template>


<style lang="less">
  @import '../../assets/variable.less';

  #result{
    padding: .6rem .8rem;
    width: auto;
    height: 65%;
    background-color: @white;
    border-radius: 5px;
    text-align: left;
    overflow: auto;
    opacity: 0;
    transition: 600ms;
    &.true{
      opacity: 1;
      transition: 600ms;
    }

    ul{
      margin: 0 0;
      padding: 0 0;
    }

  }
</style>


<script>

export default {
  name: 'result',
  props: {
    isShow: {
      type: Boolean,
      required: true
    },
    keyWord: {
      type: String,
      required: true
    },
    resData: {
      type: Array,
      required: true
    }
  },
  data() {
    return {

    }
  },
  methods: {
    goBack() {
      this.$emit('goBack');
    }
  }
}

</script>



